/*
0px - 360px:		XS Phone
360px - 576px: 		Phone
576px - 768px: 		Tablet Portrait
768px - 992px:		Tablet Landscape
992px - 1200px: 	desktop
1200px - 1920px:	normal Media queries
1920px and up:  	Full HD


// 1em = 16px

	 359   =  	22.438em	
	 576   =  	36em
	 768   =	48em
	 992   =	62em
	 1200  =	75em
	 1366  =	85.375em
	 1400  =	87.5em
	 1537  =	96.063em
	 1560  =	97.5em
	 1600  =	100em
	 1680  =	105em
	 1920  =	120em
*/
:root {
  --font-family: "SF Pro Display";
  --color-orange: #FF4E00;
  --color-white: #FFFFFF;
  --color-grey: #808080;
  --color-dark: #111112;
  --color-box-shadow: #70707029;
  --color-dark-2: #222222;
  --color-dark-3: #181818;
  --color-light-grey: #F4F6F8;
  --color-hsl-dark: 240 3% 7%;
  --color-black: #000000;
  --color-white-2: rgba(255, 77, 0, 0);
  --weight-300: 300;
  --weight-400: 400;
  --weight-500: 500;
  --weight-600: 600;
  --weight-700: 700;
  --weight-800: 800;
  --weight-900: 900;
  --font-h1:3rem;
  --font-h2:2rem;
  --font-label: 0.875rem;
}

.form-label {
  font-size: var(--font-label);
  font-weight: var(--weight-500);
  color: var(--color-dark);
}

.h1 {
  font-size: var(--font-h2);
  font-weight: var(--weight-600);
}

.main-heading {
  color: var(--color-dark);
  margin-bottom: 1.5rem;
}
.main-title {
  font-size: 1.375rem;
  color: var(--color-black);
  font-weight: var(--weight-700);
  margin: 2.687rem 0 1rem;
}
.main-title--light {
  font-weight: var(--weight-500);
}

.para-text {
  font-size: 0.9375rem;
  color: var(--color-dark);
  font-weight: var(--weight-500);
}

.p-grey {
  font-size: 0.875rem;
  color: var(--color-grey);
}

.prefield-card {
  background-color: var(--color-white);
  padding: 1.875rem;
  max-width: 859px;
  margin: 3.85rem 0;
}
@media (min-width: 48em) {
  .prefield-card {
    padding: 3.75rem;
  }
}

header {
  width: 100%;
  max-height: 6.875rem;
  background-color: var(--color-dark);
  padding: 1.1rem 0;
  position: fixed;
  z-index: 2;
  display: flex;
}
@media (min-width: 0.1em) {
  header {
    width: 100%;
    margin: 0 auto;
  }
}
@media (min-width: 75em) {
  header {
    width: 100%;
    margin: 0 auto;
  }
}
header .navbar {
  margin: 0 auto;
}
@media (min-width: 0.1em) {
  header .navbar {
    width: 100%;
  }
}
@media (min-width: 75em) {
  header .navbar {
    width: 86%;
  }
}
@media (min-width: 0.1em) {
  header .navbar .logo {
    width: 9rem;
    height: auto;
    margin-right: 0;
    margin-left: 1.3rem;
  }
}
@media (min-width: 48em) {
  header .navbar .logo {
    margin-left: 1.6rem;
  }
}
@media (min-width: 75em) {
  header .navbar .logo {
    width: 13.438rem;
    height: 3.25rem;
    margin-right: 5.5rem;
    margin-left: 0;
  }
}
header .navbar .nav-link {
  font-size: 1.125rem;
  color: var(--color-white);
  text-transform: capitalize;
  margin-right: 1.2rem;
}
header .navbar .nav-link:hover {
  color: var(--color-orange);
}
header .navbar .active {
  color: var(--color-orange);
  font-weight: var(--weight-500);
}
header .navbar .navbar-collapse {
  background-color: var(--color-dark);
  padding: 1rem;
}
header .navbar .navbar-collapse:focus {
  outline: none;
  box-shadow: 0;
}

.banner-wrapper {
  width: 100%;
  height: 107vh;
  background-image: url("../../images/banner.png");
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
@media (min-width: 0.1em) {
  .banner-wrapper .banner-content-box {
    width: 98%;
    margin: 0 auto;
  }
}
@media (min-width: 62em) {
  .banner-wrapper .banner-content-box {
    width: 43.7rem;
    margin: 0;
  }
}
.banner-wrapper .banner-content-box .hero-text {
  font-style: var(--weight-900);
  color: var(--color-white);
  line-height: 1;
  margin-bottom: 1.5rem;
}
@media (min-width: 0.1em) {
  .banner-wrapper .banner-content-box .hero-text {
    font-size: 2.9rem;
  }
}
@media (min-width: 62em) {
  .banner-wrapper .banner-content-box .hero-text {
    font-size: 4.1rem;
  }
}
.banner-wrapper .banner-content-box .sub-text {
  font-size: 1.25rem;
}
@media (min-width: 0.1em) {
  .banner-wrapper .banner-content-box .sub-text {
    color: var(--color-white);
  }
}
@media (min-width: 62em) {
  .banner-wrapper .banner-content-box .sub-text {
    color: var(--color-grey);
  }
}

.heading {
  color: var(--color-dark);
  font-weight: var(--weight-700);
  line-height: 1.1;
  margin-bottom: 1.9rem;
}
@media (min-width: 0.1em) {
  .heading {
    font-size: 2.5rem;
  }
}
@media (min-width: 62em) {
  .heading {
    font-size: 3.25rem;
  }
}

.about {
  background-color: var(--color-bg-gray);
}
@media (min-width: 0.1em) {
  .about {
    padding: 5rem 0;
    flex-direction: column-reverse;
    width: 94%;
    margin: auto;
  }
}
@media (min-width: 62em) {
  .about {
    padding: 7.75rem 0 9rem;
    flex-direction: row;
    width: 100%;
  }
}
.about .about-img {
  height: auto;
}
@media (min-width: 0.1em) {
  .about .about-img {
    width: 98%;
    margin: 2rem auto;
  }
}
@media (min-width: 62em) {
  .about .about-img {
    max-width: 40.6rem;
    width: 45%;
    max-height: 31.5rem;
    margin: 0 3.75rem 0 0;
  }
}
.about .about-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (min-width: 0.1em) {
  .about .about-content {
    width: 98%;
  }
}
@media (min-width: 62em) {
  .about .about-content {
    width: 30rem;
  }
}
.about .about-content .content {
  font-size: 1.12rem;
  color: var(--color-dark);
  font-weight: var(--weight-500);
}

.gallery {
  background-color: var(--color-white);
}
@media (min-width: 0.1em) {
  .gallery {
    padding-top: 0rem;
  }
}
@media (min-width: 62em) {
  .gallery {
    padding-top: 8.5rem;
  }
}
.gallery .sub-text {
  font-size: 1.1rem;
  color: var(--color-grey);
  margin: auto;
  text-align: center;
}
@media (min-width: 0.1em) {
  .gallery .sub-text {
    width: 100%;
  }
}
@media (min-width: 62em) {
  .gallery .sub-text {
    width: 48rem;
  }
}
.gallery .card-pic {
  height: auto;
  position: relative;
  overflow: hidden;
}
@media (min-width: 0.1em) {
  .gallery .card-pic {
    width: 33.33%;
  }
}
@media (min-width: 62em) {
  .gallery .card-pic {
    width: 20%;
  }
}
.gallery .card-pic img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.gallery .card-pic .img-overlay {
  background: rgba(0, 0, 0, 0.7);
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  opacity: 0;
  -webkit-transition: all 0.4s ease-in-out 0s;
  -moz-transition: all 0.4s ease-in-out 0s;
  transition: all 0.4s ease-in-out 0s;
}
.gallery .card-pic .content-details {
  position: absolute;
  text-align: center;
  width: 100%;
  top: 50%;
  left: 50%;
  opacity: 0;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  -webkit-transition: all 0.3s ease-in-out 0s;
  -moz-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}
.gallery .card-pic .content-details h3 {
  color: var(--color-white);
  font-weight: var(--weight-700);
  text-transform: capitalize;
  text-align: center;
}
@media (min-width: 0.1em) {
  .gallery .card-pic .content-details h3 {
    font-size: 1.1rem;
  }
}
@media (min-width: 62em) {
  .gallery .card-pic .content-details h3 {
    font-size: 1.8rem;
  }
}
.gallery .card-pic .fadeIn-top {
  top: 20%;
}
.gallery .card-pic:hover .img-overlay {
  opacity: 1;
}
.gallery .card-pic:hover .content-details {
  top: 50%;
  left: 50%;
  opacity: 1;
}

footer {
  width: 100%;
  background: var(--color-dark);
}
footer .footer-inner {
  margin: auto;
  padding-top: 7.3rem;
}
@media (min-width: 0.1em) {
  footer .footer-inner {
    width: 100%;
  }
}
@media (min-width: 62em) {
  footer .footer-inner {
    width: 75%;
  }
}
@media (min-width: 0.1em) {
  footer .footer-inner .center {
    justify-content: center;
  }
}
@media (min-width: 62em) {
  footer .footer-inner .center {
    justify-content: flex-start;
  }
}
footer .footer-inner .d-flex-footer {
  display: flex;
}
@media (min-width: 0.1em) {
  footer .footer-inner .d-flex-footer {
    flex-direction: column;
    text-align: center;
    align-items: center;
  }
}
@media (min-width: 62em) {
  footer .footer-inner .d-flex-footer {
    flex-direction: row;
    text-align: left;
    align-items: flex-start;
  }
}
footer .footer-inner .footer-heading {
  font-size: 1.625rem;
  font-weight: var(--weight-700);
  color: var(--color-white);
  text-transform: capitalize;
}
@media (min-width: 0.1em) {
  footer .footer-inner .footer-heading {
    margin-bottom: 1rem;
  }
}
@media (min-width: 62em) {
  footer .footer-inner .footer-heading {
    margin-bottom: 3rem;
  }
}
@media (min-width: 0.1em) {
  footer .footer-inner .mar-top {
    margin-top: 4rem;
  }
}
@media (min-width: 62em) {
  footer .footer-inner .mar-top {
    margin-top: 0;
  }
}
footer .footer-inner .map-box {
  height: 10.3rem;
  margin-bottom: 0.75rem;
  display: flex;
}
@media (min-width: 0.1em) {
  footer .footer-inner .map-box {
    width: 100%;
    justify-content: center;
  }
}
@media (min-width: 62em) {
  footer .footer-inner .map-box {
    width: 18rem;
    justify-content: flex-start;
  }
}
footer .footer-inner .orange-link {
  font-size: 0.875rem;
  color: var(--color-orange);
}
footer .footer-inner .footer-text {
  font-size: 1rem;
  color: var(--color-white);
}
footer .footer-inner .fs-14 {
  font-size: 14px;
}
footer .footer-inner .icon {
  width: 1rem;
  height: 1rem;
  margin-right: 1.125rem;
}