@use "../utility/reuseble" as *;
@use  "../utility" as *;



// ============header css=============

header {
    width: 100%;
    max-height: 6.875rem;
    background-color: var(--color-dark);
    padding: 1.1rem 0;
    position: fixed;
    z-index: 2;
    display: flex;
    @include mediaMin(xs){
        width: 100%;;
        margin: 0 auto;
    }
    @include mediaMin(xl){
        width: 100%;;
        margin: 0 auto;
    }
    .navbar {
        margin: 0 auto;
        @include mediaMin(xs){
            width: 100%;
        }
        @include mediaMin(xl){
            width: 86%;
        }
        
        .logo {
            @include mediaMin(xs){
                width: 9rem;
                height: auto;
                margin-right: 0;
                margin-left: 1.3rem;
            }
            @include mediaMin(md){
                margin-left: 1.6rem;
            }
            @include mediaMin(xl){
                width: 13.438rem;
                height: 3.25rem;
                margin-right: 5.5rem;
                margin-left: 0;
            }
        }
        .nav-link {
            font-size: 1.125rem;
            color: var(--color-white);
            text-transform: capitalize;
            margin-right: 1.2rem;
            &:hover {
                color: var(--color-orange);
            }
        }
        .active {
            color: var(--color-orange);  
            font-weight: var(--weight-500);  
        }
        .navbar-collapse {
            background-color: var(--color-dark);
            padding: 1rem;
            &:focus {
                outline: none;
                box-shadow: 0;
            }
        }
    }
}

//=============landing css ============ 

.banner-wrapper {
    width: 100%;
    height: 107vh;
    //height: 800px;
    background-image: url('../../images/banner.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    .banner-content-box {
        @include mediaMin(xs){
            width: 98%;
            margin: 0 auto;
        }
        @include mediaMin(lg){
            width: 43.7rem;
            margin: 0;
        }
        .hero-text {
            font-style: var(--weight-900);
            color: var(--color-white);
            line-height: 1;
            margin-bottom: 1.5rem;
            @include mediaMin(xs){
                font-size: 2.9rem;
            }
            @include mediaMin(lg){
                font-size: 4.1rem;
            }
        }
        .sub-text {
            font-size: 1.25rem;
            @include mediaMin(xs){
                color: var(--color-white);
            }
            @include mediaMin(lg){
                color: var(--color-grey);
            }
        }
    }
}
.heading {
    color: var(--color-dark);
    font-weight: var(--weight-700);
    line-height: 1.1;
    margin-bottom: 1.9rem;

    @include mediaMin(xs){
        font-size: 2.5rem;
    }
    @include mediaMin(lg){
        font-size: 3.25rem;
    }
}
.about {
    background-color: var(--color-bg-gray);
    @include mediaMin(xs){
       padding: 5rem 0;
       flex-direction: column-reverse;
       width: 94%;
       margin: auto;
    }
    @include mediaMin(lg){
        padding: 7.75rem 0 9rem;
        flex-direction: row;
        width: 100%;
    }
    .about-img {
        height: auto;
        @include mediaMin(xs){
            width: 98%;
            margin: 2rem auto;
        }
        @include mediaMin(lg){
            max-width: 40.6rem;
            width: 45%;
            max-height: 31.5rem;
            margin: 0 3.75rem 0 0;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    .about-content {
        @include mediaMin(xs){
            width: 98%;
        }
        @include mediaMin(lg){
            width: 30rem;
        }
        .content {
            font-size: 1.12rem;
            color: var(--color-dark);
            font-weight: var(--weight-500);
        }
    }
}
.gallery {
    background-color: var(--color-white);
    @include mediaMin(xs){
        padding-top: 0rem;
    }
    @include mediaMin(lg){
        padding-top: 8.5rem;
    }

    .sub-text {
        font-size: 1.1rem;
        color:var(--color-grey);
        margin: auto;
        text-align: center;
        @include mediaMin(xs){
            width: 100%;
        }
        @include mediaMin(lg){
            width: 48rem;
        }
    }
    .card-pic {
        height: auto;
        position: relative;
        overflow: hidden;
        @include mediaMin(xs){
            width: 33.33%;
        }
        @include mediaMin(lg){
            width: 20%;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        .img-overlay {
            background: rgba(0,0,0,0.7);
            position: absolute;
            height: 100%;
            width: 100%;
            left: 0;
            top: 0;
            bottom: 0;
            right: 0;
            opacity: 0;
            -webkit-transition: all 0.4s ease-in-out 0s;
            -moz-transition: all 0.4s ease-in-out 0s;
            transition: all 0.4s ease-in-out 0s;
        }
        .content-details {
            position: absolute;
            text-align: center;
            width: 100%;
            top: 50%;
            left: 50%;
            opacity: 0;
            -webkit-transform: translate(-50%, -50%);
            -moz-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
            -webkit-transition: all 0.3s ease-in-out 0s;
            -moz-transition: all 0.3s ease-in-out 0s;
            transition: all 0.3s ease-in-out 0s;

            h3{
                color: var(--color-white);
                font-weight: var(--weight-700);
                text-transform: capitalize;
                text-align: center;
                @include mediaMin(xs){
                    font-size: 1.1rem;
                }
                @include mediaMin(lg){
                    font-size: 1.8rem;
                }
            }
        }

        .fadeIn-top{
            top: 20%;
        }
        &:hover .img-overlay{
            opacity: 1;
        }
        &:hover .content-details{
            top: 50%;
            left: 50%;
            opacity: 1;
        }
    }
}

// ============== footer ==============

footer {
    width: 100%;
    background: var(--color-dark);
    
    .footer-inner {
        margin: auto;
        padding-top: 7.3rem;
        @include mediaMin(xs){
            width: 100%;
        }
        @include mediaMin(lg){
            width: 75%;
        }
        .center {
            @include mediaMin(xs){
                justify-content: center;
            }
            @include mediaMin(lg){
                justify-content: flex-start;
            }
        }
        .d-flex-footer {
            display: flex;
            @include mediaMin(xs){
                flex-direction: column;
                text-align: center;
                align-items: center;
            }
            @include mediaMin(lg){
                flex-direction: row;
                text-align: left;
                align-items: flex-start;
            }
        }
        .footer-heading {
            font-size: 1.625rem;
            font-weight: var(--weight-700);
            color: var(--color-white);
            text-transform: capitalize;
            @include mediaMin(xs){
                margin-bottom: 1rem;
            }
            @include mediaMin(lg){
                margin-bottom: 3rem;
            }
        }
        .mar-top {
            @include mediaMin(xs){
                margin-top: 4rem;
            }
            @include mediaMin(lg){
                margin-top: 0;
            }
        }
        .map-box {
            height: 10.3rem;
            margin-bottom: 0.75rem;
            display: flex;
            @include mediaMin(xs){
                width: 100%;
                justify-content: center;
            }
            @include mediaMin(lg){
                width: 18rem;
                justify-content: flex-start;
            }
        }
        .orange-link {
            font-size: 0.875rem;
            color:var(--color-orange);
        }
        .footer-text {
            font-size: 1rem;
            color: var(--color-white);
        }
        .fs-14 {
            font-size: 14px;
        }
        .icon {
            width: 1rem;
            height: 1rem;
            margin-right: 1.125rem;
        }
    }
}